<style>
  .ivu-form .ivu-form-item-label{
    color: #ddd;
    font-size: 14px;
  }
  .ivu-form-label-top .ivu-form-item-label {
    padding: 0px;
  }
</style>
<template>
  <!--下级单位基本信息-->
  <!--右侧内容总div-->
  <div class="right-container">
    <Row class="container-navigation">
      <iCol span="2">
        <img src="../../../assets/content/navigate_icon.png" height="20" width="20"/>
      </iCol>
      <iCol span="22">
        <span class="page-title">下级单位</span>
      </iCol>
    </Row>
    <div class="back-to-list" v-if="canReturn" @click="returnPage"></div>
    <!--右侧工具栏，包括检索、添加等按钮-->
    <div class="tool-panel">
      <Input v-model="searchParam" class="grey" placeholder="名称/手机号" @on-enter="searchSubOrg(1)" style="width: 300px"></Input>
      <Button type="primary" @click="searchSubOrg(1)" class="grey" style="margin-left: 5px; width: 80px;">搜索</Button>
      <Button v-if="editable" type="primary" @click="openNewOrg" class="grey" style="margin-left: 5px; width: 80px;float: right">添加</Button>
    </div>
    <!--右侧表格模块-->
    <div class="body-table organization-list">
      <!--表格位置-->
      <Table :columns="columns" border :data="org"
             class="auto-height-table grey" style="width:100%"></Table>
      <!--分页位置-->
      <div style="margin-top: 10px;overflow: hidden">
        <div style="float: right;">
          <Page @on-change="searchSubOrg" class="default" :total="pageTotal" :current="pageNum" :pageSize="pageSize" :show-total="true"></Page>
        </div>
      </div>
    </div>
    <Modal v-model="addOrgModal" class="default" title="添加下级单位" width="700" :mask-closable="false" @on-cancel="cancel('newOrgFrom')">
      <Form ref="newOrgFrom" :model="newOrg" :label-width="90"  :rules="newOrgValidate">
        <Form-item prop="name" label="名称：">
          <Input type="text" placeholder="输入名称" class="grey" v-model="newOrg.name"/>
        </Form-item>
        <myarea @select="myareaOut" :address="address"></myarea>
        <form-item prop="addrStreet" :label-width="100" label="详细地址：" style="margin-left: -10px">
          <Input  v-model="newOrg.addrStreet" placeholder="输入详细地址" class="grey" />
        </form-item>
        <Form-item prop="contactNum" label="单位电话：">
          <Input type="text" class="grey" placeholder="输入单位电话"   v-model="newOrg.contactNum" style="width:188px"/>
        </Form-item>
        <Row :gutter="8">
          <iCol span="12">
          <Form-item prop="ownerName" label="负责人：">
            <Input type="text" class="grey" placeholder="输入负责人姓名"  v-model="newOrg.ownerName" style="width:188px"/>
          </Form-item>
          </iCol>
          <iCol span="12">
          <Form-item prop="ownerTel" label="电话："  style="margin-left: 3px;">
            <Input type="text" class="grey" placeholder="输入电话" v-model="newOrg.ownerTel" style="width:188px;"/>
          </Form-item>
          </iCol>
        </Row>
        <Row :gutter="10">
        <iCol span="12">
        <Form-item prop="businessType" label="业态：">
          <Select v-model="newOrg.businessType" placeholder="选择业态"  :title="fireUnitType===undefined ? '' : fireUnitType.codeValue" size="small" class="grey" style="width:188px" @on-change="businessChange">
            <Option v-for="fireUnitType in fireUnitTypeList" :title="fireUnitType.codeValue" :value=fireUnitType.codeId style="overflow: hidden;white-space: nowrap;text-overflow: ellipsis;">{{fireUnitType.codeValue}}</Option>
          </Select>
        </Form-item>
        </iCol>
        <iCol span="12">
        <Form-item prop="supervLevel" label="消防监管等级：" :label-width="120" style="margin-left: -27px;">
          <Select v-model="newOrg.supervLevel" size="small" placeholder="选择消防监管等级" class="grey" style="width:188px;">
            <Option v-for="supervision in supervisionList" :value=supervision.codeId>{{supervision.codeValue}}</Option>
          </Select>
        </Form-item>
        </iCol>
        </Row>
        <!--<Row>-->
          <!--<Form-item prop="smallPlaces" label="九小场所：" >-->
            <!--<Checkbox :disabled="!editable" size="large" v-model="newOrg.smallPlaces"></Checkbox>-->
          <!--</Form-item>-->
        <!--</Row>-->
      </Form>
      <div slot="footer">
        <Button class="grey cancel" @click="cancel('newOrgFrom')">取消</Button>
        <Button class="grey" @click="addOrg('newOrgFrom')">保存</Button>
      </div>
    </Modal>
    <Modal v-model="modal" class="default" :title="'确定要删除吗'" width="500" :mask-closable="false">
      <div slot="footer">
        <Button class="grey cancel" @click="modal = false">取消</Button>
        <Button class="grey"  @click="quitPerson">确定</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import myArea from '../../../components/vue-myarea/area.vue';
  import FormItem from "../../../../node_modules/iview/src/components/form/form-item.vue";
  import server from "../../../config/api";
  import Vue from 'vue';
  import niceScroll from 'jquery.nicescroll';

  export default {
    data(){
      return {
        canReturn: this.getYunSessionLength('route_list') > 1,
        fireUnitType:{},
        supervisionList:[],
        fireUnitTypeList:[],
        pageSize:20,
        pageNum: undefined,
        organizationId:'',
        delOrgId:'',
        modal:false,
        self: this,
        org:[],
        searchParam: '',
        pageTotal: undefined,
        currentOrganization: {},
        addOrgModal:false,
        newOrg:{
          orgId:'',
          name: '',
          addrStreet: '',
          contactNum: '',
          ownerName: '',
          ownerTel: '',
          businessType: [],
          supervLevel: [],
          smallPlaces:false
        },
        address: {},
        newOrgValidate: {
          name: [
            { required: true, message: '名称不能为空', trigger: 'blur' }
          ],
          addrProv: [
            {required: true, message: '请选择省份', trigger: 'blur'}
          ],
          addrCity: [
            {required: true, message: '请选择城市', trigger: 'blur'}
          ],
          addrDistrict: [
            {required: true, message: '请选择县区', trigger: 'blur'}
          ],
          addrStreet: [
            {required: true, message: '详细地址不能为空', trigger: 'blur'}
          ],
        },

        columns: [
          {
            title: '序号',
            type: 'index',
            align: 'center',
            width:50
          },
          {
            title: '单位名称',
            key: 'name',
            align: 'center'
          },
          {
            title: '地址',
            key: 'address',
            align: 'center'
          },
          {
            title: '负责人',
            key: 'ownerName',
            align: 'center',
            width:80
          },
          {
            title: '电话',
            key: 'ownerTel',
            align: 'center',
            width:100
          },
          {
            title: '操作',
            align: 'center',
            width:120,
            render:(h,params)=>{
                if(this.editable){
                  return h('div', [
                    h('i-button',{
                      props:{
                        type:'text',
                        size:'small'
                      },
                      style:{
                        color:'#a9b2fd'
                      },
                      on:{
                        click:()=>{
                          this.toSubOrg(params.row)
                        }
                      }
                    },'进入'),

                    h('i-button',{
                      props:{
                        type:'text',
                        size:'small'
                      },
                      style:{
                        color:'#f9caca'
                      },
                      on:{
                        click:()=>{
                          this.delOrg(params.row.orgId)
                        }
                      }
                    },'移除')
                  ]);

                }else{
                  return h('div', [
                    h('i-button',{
                      props:{
                        type:'text',
                        size:'small'
                      },
                      style:{
                        color:'#a9b2fd'
                      },
                      on:{
                        click:()=>{
                          this.toSubOrg(params.row)
                        }
                      }
                    },'进入')
                  ]);

                }
            }
          }
        ],
      }
    },
    components: {
      FormItem,
      'myarea': myArea,
    },
    methods: {
      createOrgTable:function () {
        this.searchSubOrg(1);
      },
      businessChange:function (businessType) {
        this.fireUnitType=_.filter(this.fireUnitTypeList, {'codeId': businessType})[0];
      },
      searchSubOrg:function (pageNum) {
        this.$http.get(server.api.organization.subOrg.searchSubOrg.replace('{orgId}',this.organizationId)+"?orgName="+this.searchParam+"&pageSize="+this.pageSize+"&pageNum="+pageNum).then((response)=>{
          this.org=response.body.data.rows;
          this.pageTotal = response.body.data.totalCount;
          this.pageNum = pageNum;
          Vue.nextTick(() => {
            $(".auto-height-table .ivu-table-body").getNiceScroll().resize();
          });
        },(error) => {
          this.$Message.error('修改失败！');
        })
      },
      returnPage: function() {
        // 根据sessionStorage返回上一页
        this.popYunSession('route_list');
        let page = this.topYunSession('route_list');
        this.$router.push(page.router);
      },
      openNewOrg:function () {
        this.newOrg={};
        this.addOrgModal=true;
      },
      addOrg:function (name) {
        this.newOrg.orgClass='NORMAL_ENTITY';
        this.newOrg.parentOrgId=this.organizationId;
        this.newOrg.defaultPid=localStorage.default_pid;
        this.newOrg.addrProv=this.address.addrProv;
        this.newOrg.addrCity=this.address.addrCity;
        this.newOrg.addrDistrict=this.address.addrDistrict;
        this.$refs[name].validate((valid) =>{
          if (valid) {
            this.$http.post(server.api.organization.subOrg.addOrg,this.newOrg).then((response)=>{
              this.searchSubOrg(this.pageNum);
              this.addOrgModal=false;
              this.$Message.success('新单位添加成功!');
            },(error) => {
              this.$Message.error('新单位添加失败！');
            })
          } else {
            this.$Message.error('表单验证失败!');
          }
        })
      },
      delOrg:function (orgId) {
        this.delOrgId=orgId;
        this.modal=true;
      },

      quitPerson:function (orgId) {
        if(this.org.length==1 && this.pageNum !=1){
          this.pageNum=this.pageNum-1;
        }
        this.$http.delete(server.api.organization.subOrg.delOrg.replace('{orgId}', this.delOrgId)).then((response)=>{
          this.searchSubOrg(this.pageNum);
          this.$Message.success('新单位移除成功!');
        },(error) => {
          this.$Message.error('新单位移除失败！');
        })
        this.modal=false;
      },

      toSubOrg:function (org) {
        let page = {
          router: {
            path: '/company/base_info',
            query: {
              active_name: 'base_info'
            }
          },
          orgId: org.orgId
        };
        this.$root.eventHub.$emit('setOrgInfo', page);
      },
      myareaOut: function(d) {
        this.address = d;
        console.log(this.address);
      },
      cancel:function (name) {
        this.$refs[name].resetFields();
        this.addOrgModal=false;
      },
      loadSystemCode: function () {
        let categories=[];
        categories.push('SUPERVISION_LEVEL');
        categories.push('FIRE_UNIT_TYPE');
        this.$http.get(server.api.organization.sysCode.getSysCode+'?categories[]='+categories).then((response)=>{
          this.systemCodeList=response.body.data;
          this.supervisionList=_.filter(this.systemCodeList, {'codeCategory': 'SUPERVISION_LEVEL'});
          this.fireUnitTypeList=_.filter(this.systemCodeList, {'codeCategory': 'FIRE_UNIT_TYPE'});
        },(error)=>{
          this.$Message.error("系统数据获取失败");
        })
      }
    },
    computed:{
      editable: function() {
        return this.hasPermission(this.$YDAuthCode.NormalEntity.OrganizationMgmt);
      }
    },
//    watch:{
//      '$route':function (route) {
//        let orgList = this.getYunSession('org_list');
//        this.organizationId = orgList.length === 0 ? localStorage.current_org_id : orgList[orgList.length-1];
//        this.createOrgTable();
//      }
//    },
    mounted(){
      let page = this.topYunSession('route_list');
      if(page === null || page.orgId === undefined) {
        // 根节点
        this.organizationId = localStorage.current_org_id;
      } else {
        this.organizationId = page.orgId;
      }
      this.createOrgTable();
      this.loadSystemCode();
      Vue.nextTick(() => {
        $(".auto-height-table .ivu-table-body").niceScroll({
          cursorcolor: "rgb(99, 114, 131)",
          cursorwidth: "7px",
          cursoropacitymax: 0.4,
          cursorborder: "", // css definition for cursor border
          cursorborderradius: "",
          hidecursordelay: 1000,
        });
      });
    }
  }
</script>



